import React from "react";
import { Carousel } from "react-bootstrap";
import aladin from "../img/aladin.jpg";
import marvel from "../img/marvel.jpg";
import thor from "../img/thor.jpg";
import Beetle from "../img/Blue-Beetle.jpg";

function AutoImageCarousel() {
  const data = [
    {
      title: "Aladdin",
      img: aladin,
      desc: `Aladdin, a kind thief, woos Jasmine, the princess of Agrabah, with
      the help of Genie. When Jafar, the grand vizier, tries to usurp the
      king, Jasmine, Aladdin and Genie must stop him from succeeding.`,
    },
    {
      title: "The Marvels",
      img: marvel,
      desc: `Carol Danvers gets her powers entangled with those of Kamala Khan
      and Monica Rambeau, forcing them to work together to save the
      universe.`,
    },
    {
      title: "Thor: Love and Thunder",
      img: thor,
      desc: `Thor enlists the help of Valkyrie, Korg and ex-girlfriend Jane
      Foster to fight Gorr the God Butcher, who intends to make the gods
      extinct.`,
    },
    {
      title: "Blue Beetle",
      img: Beetle,
      desc: `An alien scarab chooses Jaime Reyes to be its symbiotic host, bestowing the recent college graduate with a suit of armor that's capable of extraordinary powers, forever changing his destiny as he becomes the superhero known as Blue Beetle.`,
    },
  ];
  return (
    <Carousel interval={1200}>
      {data.map((e) => {
        return (
          <Carousel.Item>
            <img className="d-block w-100" src={e.img} alt="First slide" />
            <Carousel.Caption>
              <h1>{e.title}</h1>
              <p>{e.desc}</p>
            </Carousel.Caption>
          </Carousel.Item>
        );
      })}
    </Carousel>
  );
}

export default AutoImageCarousel;
