import React from "react";
import "./Card.css"
import {
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
  MDBCardImage,
  MDBBtn,
  MDBRipple,
} from "mdb-react-ui-kit";


const Card = ({ title, poster, description }) => {
  return (
    <MDBCard className="bg-black border">
      <MDBRipple
        rippleColor="light"
        rippleTag="div"
        className="bg-image hover-overlay"
      >
        <MDBCardImage
          src={poster}
          fluid
          alt="..."
        />
        <a>
          <div
            className="mask"
            style={{ backgroundColor: "rgba(251, 251, 251, 0.15)" }}
          ></div>
        </a>
      </MDBRipple>
      <MDBCardBody>
        <MDBCardTitle className="text-white">{title}</MDBCardTitle>
        <MDBCardText className="text-secondary description">
         {description}
        </MDBCardText>
        <MDBBtn href="#" className="btn-danger" style={{ backgroundColor: "#fd1c0b" }}>Watch Now</MDBBtn>
      </MDBCardBody>
    </MDBCard>
  )
}

export default Card