import React from "react";

const Footer = () => {
  return (
    <section className="">
      <footer className="text-white text-center text-md-start mb-5 bg-black">
        <div className="container p-4">
          <div className="row"></div>
        </div>

        <div
          className="text-center p-3 bg-black text-white fixed-bottom"
          style={{ backgroundColor: "rgba(0, 0, 0, 0.05)" }}
        >
          © 2024&nbsp;-&nbsp;
          <a
            className="text-decoration-none" style={{color:"#fd1c0b"}}
            href="https://netflix.com/"
          >
            NETFLIX
          </a>
        </div>
      </footer>
    </section>
  );
};

export default Footer;
