import React from "react";
import logo from "../img/Netflix-Logo.png";
import { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";

const NavBar = () => {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  return (
    <nav className="navbar navbar-expand-lg bg-black shadow sticky-top">
      <div className="container-fluid">
        <a href="#"><img src={logo} alt="" style={{ width: "120px" }} /></a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div
          className="collapse navbar-collapse justify-content-center"
          id="navbarNav"
        >
          <ul className="navbar-nav ">
            <li className="nav-item ">
              <a className="nav-link text-white" aria-current="page" href="#">
                MOVIES
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link text-white" href="#">
                WEBSERIES
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link text-white" href="#">
                TV-SHOWS
              </a>
            </li>
            <li className="nav-item">
              <a href="#" className="nav-link text-white">
                PRICING
              </a>
            </li>
          </ul>
        </div>
        <Button
          style={{ backgroundColor: "#fd1c0b" }}
          onmouseover="this.style.backgroundColor='green'; this.style.color='black';"
          onmouseout="this.style.backgroundColor='blue'; this.style.color='white';"
          onClick={handleShow}
        >
          Login
        </Button>

        <Modal show={show} onHide={handleClose}>
          <Modal.Body className="bg-dark text-white">
            <form>
              {/* Email input */}
              <div data-mdb-input-init="" className="form-outline mb-4">
                <input
                  type="email"
                  id="form2Example1"
                  className="form-control"
                />
                <label className="form-label" htmlFor="form2Example1">
                  Email address
                </label>
              </div>

              {/* Password input */}
              <div data-mdb-input-init="" className="form-outline mb-4">
                <input
                  type="password"
                  id="form2Example2"
                  className="form-control"
                />
                <label className="form-label" htmlFor="form2Example2">
                  Password
                </label>
              </div>

              {/* 2 column grid layout for inline styling */}
              <div className="row mb-4">
                <div className="col d-flex justify-content-center">
                  {/* Checkbox */}
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value={true}
                      id="form2Example34"
                      checked={true}
                    />
                    <label
                      className="form-check-label"
                      htmlFor="form2Example34"
                    >
                      {" "}
                      Remember me{" "}
                    </label>
                  </div>
                </div>

                <div className="col">
                  {/* Simple link */}
                  <a href="#!">Forgot password?</a>
                </div>
              </div>

              {/* Submit button */}
              <button
                data-mdb-ripple-init=""
                type="button"
                className="btn btn-primary btn-block mb-4"
              >
                Sign in
              </button>

              {/* Register buttons */}
              <div className="text-center">
                <p>
                  Not a member? <a href="#!">Register</a>
                </p>
                <p>or sign up with:</p>
                <button
                  data-mdb-ripple-init=""
                  type="button"
                  className="btn btn-secondary btn-floating mx-1"
                >
                  <i className="fab fa-facebook-f" />
                </button>

                <button
                  data-mdb-ripple-init=""
                  type="button"
                  className="btn btn-secondary btn-floating mx-1"
                >
                  <i className="fab fa-google" />
                </button>

                <button
                  data-mdb-ripple-init=""
                  type="button"
                  className="btn btn-secondary btn-floating mx-1"
                >
                  <i className="fab fa-twitter" />
                </button>

                <button
                  data-mdb-ripple-init=""
                  type="button"
                  className="btn btn-secondary btn-floating mx-1"
                >
                  <i className="fab fa-github" />
                </button>
              </div>
            </form>
          </Modal.Body>
        </Modal>
      </div>
    </nav>
  );
};

export default NavBar;
